<template>
  <v-container>
    <v-layout
      align-center
      wrap
      column
    >
      <v-flex>
        <v-btn @click="bounce = !bounce">
          bounce
        </v-btn>
        <transition name="bounce">
          <v-img v-if="bounce" class="animation_img" :src="require('@/assets/logo.png')"></v-img>
        </transition>
      </v-flex>
      <v-flex>
        <v-btn @click="roll = !roll">
          roll
        </v-btn>
        <transition name="roll">
          <v-img v-if="roll" class="animation_img" :src="require('@/assets/logo.png')"></v-img>
        </transition>
      </v-flex>
      <v-flex>
        <v-btn @click="animated = !animated">
          animated
        </v-btn>
        <transition name="animate" enter-active-class="animated flipInY" leave-active-class="animated zoomOutRight">
          <v-img v-if="animated" class="animation_img" :src="require('@/assets/logo.png')"></v-img>
        </transition>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      show: true,
      bounce: false,
      roll: false,
      animated: false
    };
  }
}
</script>

<style>
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.animation_img{
  height: 200px;
  width: 200px;
}
.bounce-enter-active{
  animation: bounce-in .5s; 
}
.bounce-leave-active{
  animation: bounce-in .5s reverse;
}
@keyframes bounce-in {
  0%{
    transform: scale(0);
  }
  50%{
    transform: scale(1.5);
  }
  100%{
    transform: scale(1);
  }
}
.roll-enter-active{
  animation: roll-in .5s; 
}
.roll-leave-active{
  animation: roll-in .5s reverse;
}
@keyframes roll-in {
  0%{
    transform: scale(0) rotateZ(0deg) translateX(-250px);
    opacity: 0;
  }
  25%{
    opacity: 0.5;
  }
  100%{
    transform: scale(1) rotateZ(360deg) translateX(0px);
    opacity: 1;
  }
}
</style>
